import { createComponent, setAttribute, setComponentProps } from ".";

export function diff(dom, vnode, container) {
  // ÂØπÊØîËäÇÁÇπÁöÑÂèòÂåñ
  const ret = diffNode(dom, vnode);
  if (container) {
    container.appendChild(ret);
  }
  return ret;
}

function diffNode(dom, vnode) {
  let ret = dom;
  if (vnode === undefined 
    || vnode === null
    || vnode === ''
  ) return;

    // Â¶ÇÊûúvnodeÊòØÊï∞ÂÄº
    if (typeof vnode === 'number') {
      vnode = String(vnode);
    }

  // Â¶ÇÊûúvnodeÊòØÂ≠óÁ¨¶‰∏≤
  if (typeof vnode === 'string') {
    if(dom && dom.nodeType === 3) {
      if (dom.textContext !== vnode) {
        // Êõ¥Êñ∞ÊñáÊú¨ÂÜÖÂÆπ
        dom.textContext = vnode;
      }
    } else {
      ret = document.createTextNode(vnode);
      if (dom && dom.parentNode) {
        dom.parentNode.replaceNode(ret, dom);
      }
    }
    return ret;
  }
  // ÈùûÊñáÊú¨DOMËäÇÁÇπ
  if(!dom) {
    ret = document.createElement(vnode.tag);
  }
  // ÊØîËæÉÂ≠êËäÇÁÇπÔºàdomËäÇÁÇπÂíåÁªÑ‰ª∂Ôºâ
  if (vnode.children && vnode.children.length > 0 || (ret.childNodes && ret.childNodes.length > 0)) {
    // ÂØπÊØîÁªÑ‰ª∂ ÊàñËÄÖÂ≠êËäÇÁÇπ
    diffChildren(ret, vnode.children);
  }
  diffAttribute(ret, vnode);
  return ret;
}

function diffComponent(dom, vnode) {
  let comp = dom;
  // Â¶ÇÊûúÁªÑ‰ª∂Ê≤°ÊúâÂèòÂåñÔºåÈáçÊñ∞ËÆæÁΩÆprops
  if (comp && comp.constructor === vnode.tag) {
    // ÈáçÊñ∞ËÆæÁΩÆprops
    setComponentProps(comp, vnode.attrs);
    // ËµãÂÄº
    dom = comp.base;
  } else {
    // ÁªÑ‰ª∂Á±ªÂûãÂèëÁîüÂèòÂåñ
    if(comp) {
      // ÂÖàÁßªÈô§Â∞±ÁªÑ‰ª∂
      unmountComponent(comp);
      comp = null;
    }

    // 1.ÂàõÂª∫Êñ∞zÁªÑ‰ª∂
    comp = createComponent(vnode.tag, vnode.attrs);

    // 2.ËÆæÁΩÆÁªÑ‰ª∂Â±ûÊÄß
    setComponentProps(comp, vnode.attrs)
    // 3.ÁªôÂΩìÂâçÊåÇËΩΩbase
    dom = comp.base;
  }
  return dom;
}

function unmountComponent(com) {
  removeNode(comp.base);
}

function removeNode(dom) {
  if (dom && dom.parentNode) {
    dom.parentNode.removeNode(dom)
  }
}

function diffChildren(dom, vchildren) {
  const domChildren = dom.childNodes;
  const children = [];
  const keyed = {};

  // Â∞ÜÊúâkeyÁöÑËäÇÁÇπÔºàÁî®ÂØπË±°‰øùÂ≠òÔºâÂíåÊ≤°ÊúâkeyÁöÑËäÇÁÇπÔºàÁî®Êï∞ÁªÑ‰øùÂ≠òÔºâÂàÜÂºÄ

  if(vchildren && vchildren.length > 0) {
    let min = 0;
    let childrenLen = children.length;
    [...vchildren].forEach((vchild, i) => {
      // Ëé∑ÂèñËôöÊãüDOM‰∏≠ÊâÄÊúâÁöÑkey
      const key = vchild.key;
      let child;
      if (key) {
        // Â¶ÇÊûúÊúâkeyÔºåÊâæÂà∞ÂØπÂ∫îkeyÂÄºÂæóËäÇÁÇπ
        if (keyed[key]) {
          child = keyed[key];
          keyed[key] = undefined;
        }
      } else if (childrenLen > min) {
        // Â¶ÇÊûúÊ≤°ÊúâkeyÔºåÂàô‰ºòÂÖàÊâæÂà∞Á±ªÂûãÁõ∏ÂêåÁöÑËäÇÁÇπ
        for (let j = min; j < childrenLen; j ++) {
          let c = children[j];
          if(c) {
            child = c;
            children[j] = undefined;
            if (j === childrenLen - 1) childrenLen--;
            if (j === min) min++;
            break;
          }
        }
      }
      // ÂØπÊØî
      child = diffNode(child, vchild);
      // Êõ¥Êñ∞DOM
      const f = domChildren[i];

      if(child && child !== dom && child !== f) {
        // Â¶ÇÊûúÊõ¥Êñ∞ÂâçÁöÑÂØπÂ∫î‰ΩçÁΩÆ‰∏∫Á©∫ÔºåËØ¥ÊòéÊ≠§ËäÇÁÇπÊòØÊñ∞Â¢ûÁöÑ
        if(!f) {
          dom.appendChild(child);

          // Â¶ÇÊûúÊõ¥Êñ∞ÂêéÁöÑËäÇÁÇπÂíåÊõ¥Êñ∞ÂâçÂØπÂ∫î‰ΩçÁΩÆÁöÑ‰∏ã‰∏Ä‰∏™ËäÇÁÇπ‰∏ÄÊ†∑
        } else if (child === f.nextSibling) {
          reomoveNode(f);

          // Â∞ÜÊõ¥Êñ∞ÂêéÁöÑËäÇÁÇπÁßªÂä®Âà∞Ê≠£Á°ÆÁöÑ‰ΩçÁΩÆ
        } else {
          dom.insertBefore(child, f);
        }
      }
    })
  }
}

function diffAttribute(dom, vnode) {
  // ‰øùÂ≠ò‰πãÂâçÁöÑDOMÁöÑÊâÄÊúâÂ±ûÊÄß
  const oldAttrs = {};
  const newAttrs = vnode.attrs;
  // dom ÊòØÂéüÊúâÁöÑËäÇÁÇπÂØπË±° vnode ËôöÊãüDOM
  const domAttrs = dom.attributes;
  console.log('domAttrs', domAttrs);
  [...domAttrs].forEach(item => {
    oldAttrs[item.name] = item.value;
  });

  // ÊØîËæÉ
  // Â¶ÇÊûúÂéüÊù•Â±ûÊÄßË∑üÊñ∞ÁöÑÂ±ûÊÄßÂØπÊØîÔºå‰∏çÂú®Êñ∞ÁöÑÂ±ûÊÄßÂè™ÔºåÂàôÂ∞ÜÂÖ∂ÁßªÈô§Êéâ
  for(let key in oldAttrs) {
    if(!(key in newAttrs)) {
      setAttribute(dom, key, undefined);
    }
  }

  for(let key in newAttrs) {
    if (oldAttrs[key] !== newAttrs[key]) {
      // ÂÄº‰∏çÂêåÔºåÊõ¥Êñ∞ÂÄº
      setAttribute(dom, key, newAttrs[key]);
    }
  }
}


